import React, { Component } from 'react';
import './Skills.css';
import CommonButton from '../Button';
import SkillComponent from './SkillComponent';
import back from '../Personal/sbg.jpg';

class Skills extends Component {
  componentDidMount() {
    document.body.style.backgroundImage = `url(${back})`;
    document.body.style.backgroundSize = 'cover';
  }

  render() {
    return (
      <div className="top">
        <h2 className="animated bounceInDown">
          What I can bring to the table?
        </h2>
        <div className="table no-gutters">
          <div className="row">
            <div className="col no-gutters">
              <div className="animated slideInDown">
                <img src="https://img.icons8.com/nolan/96/web.png" alt="web" />
                <h3>Web</h3>
              </div>
              <SkillComponent
                src="https://img.icons8.com/nolan/48/react-native.png"
                value="95"
                text="React"
                color="success"
              />
              <SkillComponent
                src="https://img.icons8.com/windows/48/000000/django.png"
                value="95"
                text="Django"
                color="success"
              />
              <SkillComponent
                src="https://img.icons8.com/color/48/000000/bootstrap.png"
                value="95"
                text="BootSTrap 4"
                color="success"
              />
              <SkillComponent
                src="https://img.icons8.com/color/48/000000/javascript.png"
                value="95"
                text="JavaScript"
                color="success"
              />
              <SkillComponent
                src="https://img.icons8.com/color/48/000000/nodejs.png"
                value="85"
                text="NodeJS"
                color="success"
              />
              <SkillComponent
                src="https://img.icons8.com/ios-filled/48/000000/jquery.png"
                value="80"
                text="jQuer"
                color="success"
              />
              <SkillComponent
                src="https://img.icons8.com/color/48/000000/angularjs.png"
                value="75"
                text="Angular"
                color="success"
              />
            </div>

            <div class="col">
              <div className="animated slideInDown">
                <img
                  src="https://img.icons8.com/color/96/000000/the-earths-inner-core.png"
                  alt="core"
                />
                <h3>Core</h3>
              </div>
              <SkillComponent
                src="https://img.icons8.com/color/48/000000/python.png"
                value="95"
                text="Python"
                color="warning"
              />
              <SkillComponent
                src="https://img.icons8.com/color/48/000000/java-coffee-cup-logo.png"
                value="95"
                text="Java"
                color="warning"
              />
              <SkillComponent
                src="https://img.icons8.com/color/48/000000/kotlin.png"
                value="95"
                text="Kotlin"
                color="warning"
              />
              <SkillComponent
                src="https://img.icons8.com/color/48/000000/c-programming.png"
                value="90"
                text="C Language"
                color="warning"
              />
              <SkillComponent
                src="https://img.icons8.com/color/48/000000/c-plus-plus-logo.png"
                value="85"
                text="C++"
                color="warning"
              />
              <SkillComponent
                src="https://img.icons8.com/nolan/48/html-filetype.png"
                value="95"
                text="HTML"
                color="warning"
              />
              <SkillComponent
                src="https://img.icons8.com/nolan/48/css-filetype.png"
                value="95"
                text="CSS"
                color="warning"
              />
            </div>

            <div class="col">
              <div className="animated slideInDown">
                <img
                  src="https://img.icons8.com/clouds/100/000000/window-other.png"
                  alt="other"
                  color=""
                />
                <h3>other</h3>
              </div>
              <SkillComponent
                src="https://img.icons8.com/dusk/48/000000/android.png"
                value="95"
                text="Android"
                color="danger"
              />
              <SkillComponent
                src="https://img.icons8.com/dusk/48/000000/docker.png"
                value="90"
                text="Docker"
                color="danger"
              />
              <SkillComponent
                src="https://img.icons8.com/color/48/000000/kubernetes.png"
                value="90"
                text="Kubernetes"
                color="danger"
              />
              <SkillComponent
                src="https://img.icons8.com/color/48/000000/postgreesql.png"
                value="85"
                text="PostGreSQL"
                color="danger"
              />
              <SkillComponent
                src="https://img.icons8.com/nolan/48/sql.png"
                value="85"
                text="SQLite 3"
                color="danger"
              />
            </div>
          </div>
        </div>

        <div className="animated fadeIn delay-3s skillBtn">
          <CommonButton
            content="Work Experience & Education"
            address="/edex"
            color="success"
          />
        </div>
      </div>
    );
  }
}

export default Skills;
