import React, { Component } from 'react';
import './Edex.css';
import CommonButton from '../Button';
import back from '../Personal/ebg.jpg';

class Edex extends Component {
  componentDidMount() {
    document.body.style.backgroundImage = `url(${back})`;
    document.body.style.backgroundSize = 'cover';
    document.body.style.backgroundRepeat = 'no-repeat';
  }
  render() {
    return (
      <div className="edex">
        <h2 className="animated bounceInDown">Work Experience</h2>
        <div className="animated slideInLeft ontro">
          <h4 className="common">
            ONTRO <h6>January, 2019 - April, 2019 (Telangana, India)</h6>
          </h4>
          <h5 className="common">
            Software Development and Operations Executive
          </h5>
          <p>
            Designed a basketball module for the company website and an android
            app which is used in 6+ countries and have 5,00,000+ users.
            Implemented the design on ReactJS, NodeJS, Express and Android
            studio with Java and Kotlin. The main aim of the module was to
            inculcate basketball rules so that the game can be automated.
            Developed an Algorithm where the website takes the number of
            participants and pools them into different categories so that every
            participant gets a fair chance to compete and win. Tested the
            algorithm in 60+ events with 10,000+ users and ensured that the
            algorithm worked efficiently and kept updating it with modern
            technologies.
          </p>
        </div>

        <div className="animated slideInRight admy">
          <h4 className="common">
            ADMYBRAND <h6>April, 2019 - August, 2019 (Telangana, India)</h6>
          </h4>
          <h5 className="common">Full Stack Developer</h5>
          <p>
            Developed 2 sub-portals using ReactJS, Bootstrap, MongoDB, Express,
            NodeJS for information gathering and data handling which is used in
            8+ countries with 10,00,000+ users. Fixed 4+ existing routing and
            code bugs. Improved performance and efficiency of existing
            algorithms by implementing new and modern methods. Lead at team of 4
            for creating other sub-portals for the company. Remodeled the
            portals according to customers feedback. Configured OAuth for easy
            and convinient access. Performed CRUD operations on the existing
            portals.
          </p>
        </div>
        <div>
          <h2 className="animated bounceInUp ">Education</h2>
          <div className="row">
            <div className="col">
              <div className=" animated slideInLeft ed">
                <h5 className="common">
                  CALIFORNIA STATE UNIVERSITY EAST BAY{' '}
                  <h6 className="common">August, 2019 - May, 2021</h6>
                </h5>
                <h5 className="common">Masters in Computer Science</h5>
                <h5 className="common">GPA: 3.5</h5>
                <h6 className="common">Hayward, CA</h6>
              </div>
            </div>
            <div className="col">
              <div className="animated slideInRight edu">
                <h5 className="common">
                  CVR College Of Engineering <h6>August, 2015 - May, 2019</h6>
                </h5>
                <h5 className="common">Bachelors in Electrical Engineering</h5>
                <h5 className="common">GPA: 3.4</h5>
                <h6 className="common">Telagana, India</h6>
              </div>
            </div>
          </div>
        </div>
        <div className="animated fadeIn delay-2s edexBtn">
          <CommonButton content="Projects" address="projects/" color="info" />
        </div>
      </div>
    );
  }
}

export default Edex;
