import React, { Component } from 'react';
import './Projects.css';
import ProjectsComponent from './ProjectsComponent';
import bb from '../Personal/Burger-Builder.png';
import streams from '../Personal/streams.png';
import coder from '../Personal/coder.png';
import swoosh from '../Personal/swoosh.png';
import tours from '../Personal/tours.png';
import todowoo from '../Personal/todowoo.PNG';
import tracalorie from '../Personal/tracalorie.PNG';
import gitpf from '../Personal/gitpf.PNG';
import youtube from '../Personal/youtube.PNG';

class Projects extends Component {
  componentDidMount() {
    document.body.style.backgroundColor = 'skyblue';
  }
  render() {
    return (
      <div className="projects">
        <h1 className="animated slideInDown">My Projects</h1>
        <div className="grid">
          <div className="animated slideInLeft row no-gutters row1">
            <div className="col no-gutters">
              <ProjectsComponent
                title="Burger Builder"
                text="Created a Web application using React and Redux. A web application where one can create an account and build burgers and order them. Technologies used are React router, Thunk, Axios, Jest/Enzyme, WebPack, Bable & Firebase."
                src={bb}
                link="https://burger-builder-application.firebaseapp.com/"
              />
            </div>
            <div className="col no-gutters">
              <ProjectsComponent
                title="Twitch-Clone"
                text="With the help of React, Redux, NodeJS, Axios, and React Router created a twitch-clone. Implemented all the operations of twitch. One can create an account using OAuth. Account holders can Stream live videos and Create, Edit, Update and Delete existing content."
                src={streams}
                link="https://github.com/shethabhish/twitch-clone"
              />
            </div>
            <div className="col no-gutters">
              <ProjectsComponent
                title="Tours Website"
                text="Developed a Full Stack Web Application to manage your tours around the world. Designed the front end with ReactJS, Flexbox and SVG, and the backend with Node.js/Express, Mocha, Chai JSON, and REST API. Implemented a bit of web security also."
                src={tours}
                link="https://github.com/CSUEB-DrB-CS/assignments-shethabhish"
              />
            </div>
          </div>
          <div className="animated slideInRight row no-gutters">
            <div className="col no-gutters">
              <ProjectsComponent
                title="TodoWoo"
                text="Developed a Todo List App using Django, BootStrap4, Django Forms and SQLite3. Enabled user privacy and secure authentication. One can create daily todo's, mark them as completed, important and delete them."
                src={todowoo}
                link="http://shethabhish.pythonanywhere.com/"
              />
            </div>
            <div className="col no-gutters">
              <ProjectsComponent
                title="Youtube-mini-Clone"
                text="Created an YouTube mini clone with the help of YouTube API, ReactJS, Axios, Redux, and Redux Thunk. Facilitated all major YouTube features, mainly created this app to get a break from the advertisements."
                src={youtube}
                link="https://github.com/shethabhish/youtubemini-clone"
              />
            </div>
            <div className="col no-gutters">
              <ProjectsComponent
                title="Swoosh"
                text="Designed an Android app in which you can locate nearby basketball courts and show your presence on the court. Used Google Maps API for the location services and designed it with Kotlin, Java, XML and firebase."
                src={swoosh}
                link="https://github.com/shethabhish/Swoosh"
              />
            </div>
          </div>
          <div className="animated slideInUp row no-gutters">
            <div className="col no-gutters">
              <ProjectsComponent
                title="CoderSwag"
                text="Developed an Android online shopping app with Kotlin, Java, XML, SQLite and Firebase, where you can buy Coder Swag merchandise."
                src={coder}
                link="https://github.com/shethabhish/CoderSwag"
              />
            </div>
            <div className="col no-gutters">
              <ProjectsComponent
                title="Git Profile Finder"
                text="Using the GitHub API designed a website using JavaScript where recruiters and others can find GitHub profiles of anyone."
                src={gitpf}
                link="https://github.com/shethabhish/GitProfileFinder"
              />
            </div>
            <div className="col no-gutters">
              <ProjectsComponent
                title="Tracalorie"
                text="Just an Simple web application where anyone can input the food type to calculate calories and keep track of them."
                src={tracalorie}
                link="https://github.com/shethabhish/Tracalorie"
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Projects;
