import React from 'react';
import { ProgressBar } from 'react-bootstrap';
import './SkillComponent.css';

const SkillComponent = (props) => {
  return (
    <div className="container">
      <div className="animated rotateIn no-gutters icons">
        <img src={props.src} alt="" />
      </div>
      <div className="animated fadeIn delay-2s no-gutters bars">
        <ProgressBar
          variant={props.color}
          className="p2"
          animated
          now={props.value}
          label={props.text}
        />
      </div>
    </div>
  );
};

export default SkillComponent;
