import React, { Component } from 'react';
import './Home.css';
import CommonButton from '../Button';
import back from '../Personal/bg.jpg';
import Intro from '../About/Intro';

class Home extends Component {
  toggle() {
    return <Intro />;
  }
  componentDidMount() {
    document.body.style.backgroundImage = `url(${back})`;
    document.body.style.backgroundSize = 'cover';
    document.body.style.backgroundRepeat = 'no-repeat';
  }

  render() {
    return (
      <div>
        <div className="animated bounceInDown content">
          Hi! This is Shethabhish
        </div>

        <div className="animated fadeInLeft homeBtn">
          <CommonButton
            content="Let's Start"
            address="/about"
            color="warning"
          />
        </div>
      </div>
    );
  }
}

export default Home;
